<template>
    <div class="totalSurface">
        <!-- 头部 -->
        <navigation-bar :navBarStyle="headerStyle"  @onLeftClick="onLeftClick" :pageName="title" :leftImgType="imgType">
           
       </navigation-bar>

       <section class="conBox">
           <div id="con" ref="chart">

           </div>
       </section>

    </div>
</template>
<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import {getFinishQuestVoteData} from '@/services/servers/question';

export default {
    data(){
        return{
            title:'问卷报表',
            headerStyle:{
                'backgroundColor':'#17B7BD',
                'color':'#fff'
                
            },
            imgType:'1',
        }
    },
    components:{
        NavigationBar,
       
    },
    mounted(){
        let item = this.$route.query;
        this.getData(item);
    },
    methods:{
        onLeftClick(){
            this.$router.go(-1);
        },
        getData(item){
            let data = {
                "PUBLISH_CODE":item.PUBLISH_CODE,
                "ROOM_CODE":this.$userInfo.ROOM_CODE || '',
                "AREA_CODE":this.$userInfo.AREA_CODE || '',
                "USER_CODE":this.$userInfo.OWNER_CODE || ''
            };
            data = {data:JSON.stringify(data)};
            getFinishQuestVoteData(data).then(
                res =>{
                    this.init(res)
                },
                err=>{

                }
            )

        },
        init(arr){
           
            let myChart = this.$echarts.init(this.$refs.chart);
            let data = [];
            let labelData = [];
            arr.percentList.forEach((value,index) => {
                let con = {};
                con.name = value.ITEM_NAME;
                con.value = value.ITEM_COUNT;
                // con.itemStyle = {};
                // con.itemStyle.color = this.radomColor();
                data.push(con);
                labelData.push(value.ITEM_NAME);
            })
            console.log(data);
            let option = {
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: 'vertical',
                    left:'2%',
                    top:'4%'
                },
                series : [
                    {
                    name: '投票结果',
                    type: 'pie',
                    radius : '55%',
                    center: ['50%', '50%'],
                    data:data,
                    itemStyle: {
                        normal:{
                        label:{
                            show:true,
                            formatter: '{d}%'
                        },
                        labelLine:{
                            show:true
                        }
                        },
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                    }
                ],
          
            };
            myChart.setOption(option);
        },
       

    },

}
</script>
<style scoped>
.totalSurface{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column
}
.totalSurface .conBox{
    width: 100%;
    flex: 1
}
.totalSurface #con{
    width: 100%;
    height: 100%;
}
</style>