import http from '../../http';
import {Toast} from "vant";

// 获取问卷列表（新接口）
const getPaperList = (reqData) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-property-question/paper/getPaperList', reqData).then(result => {
            resolve(result)
        }).catch(error => {
            Toast('网络异常，请稍后重试！');
            reject(error);
        })
    });
}

